import { h, Component } from 'preact';
import { route } from 'preact-router';
import Card from 'preact-material-components/Card';
import 'preact-material-components/Card/style.css';
import 'preact-material-components/Button/style.css';
import api from 'lineageos-stats';
import style from './style';

export default class Dashboard extends Component {

	state = {
		installs: 'Unknown',
		lastUpdated: 'Unknown',
		device: 'Unknown',
		country: 'Unknown'
	};

	goToDeviceList = () => route('/devices');
	goToCountryList = () => route('/countries');

	async componentWillMount() {
		const stats = await this.fetchStats();
		this.setState(stats);
	}

	async fetchStats() {
		const stats = await Promise.all([
			api.getTotalInstalls(),
			api.getLastUpdated(),
			api.getModels(),
			api.getCountries()
		]);
		return {
			installs: stats[0],
			lastUpdated: stats[1],
			device: stats[2][0],
			country: stats[3][0]
		};
	}

	render(props, { installs, lastUpdated, device, country }) {
		return (
			<section id={style.dashboard}>
				<Card className={style.card}>
					<Card.Primary>
						<Card.Title large className={style.title}>{ installs }</Card.Title>
						<Card.Subtitle>Active installs</Card.Subtitle>
						<Card.Subtitle>{ lastUpdated }</Card.Subtitle>
					</Card.Primary>
				</Card>
				<Card className={style.card}>
					<Card.Primary>
						<Card.Title large className={style.title}>{ device }</Card.Title>
						<Card.Subtitle>Most active device</Card.Subtitle>
					</Card.Primary>
					<Card.Actions>
						<Card.Action onClick={this.goToDeviceList}>More</Card.Action>
					</Card.Actions>
				</Card>
				<Card className={style.card}>
					<Card.Primary>
						<Card.Title large className={style.title}>{ country }</Card.Title>
						<Card.Subtitle>Most active country</Card.Subtitle>
					</Card.Primary>
					<Card.Actions>
						<Card.Action onClick={this.goToCountryList}>More</Card.Action>
					</Card.Actions>
				</Card>
			</section>
		);
	}
}



